a {
  color: $white;
  text-decoration: none;

  // text-decoration: none;
}

.section {
  display: flex;
  justify-content: center;
  margin: 30px 0;
  &__diverror {
    border: 2px solid $yellow;
    border-radius: 10px;
    margin: 10px;
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 600px;
    height: 90px;

    &--msn {
      color: $white;
    }
  }
}

.icon {
  background-color: $black;
  width: 20px;
  height: 20px;
  &:hover {
    background-color: $secondfont-color;
    border-color: $secondfont-color;
  }
}

.pageerror {
  font-size: 30px;
  color: $yellow;
  justify-content: center;
  text-align: center;
  padding-top: 100px;
}
